import * as React from "react"
import { Link } from "gatsby"
import { Menu, X } from "lucide-react"

// Using Font Awesome via global CSS (e.g. included in layout.html or gatsby-browser.js)

const BLOG_CATEGORIES = [
  "Guest Experience",
  "Host Stories",
  "Hosting 101",
  "Hosting Tools",
  "Insights & Trends",
  "Managing Property",
  "Pricing Strategies",
  "Scaling Tips",
]

const slugifyCategory = name =>
  name
    .toLowerCase()
    .replace(/[^a-z0-9]+/g, "-")
    .replace(/(^-|-$)/g, "")

const Header = () => {
  const [isBlogOpen, setIsBlogOpen] = React.useState(false)
  const [isCoursesOpen, setIsCoursesOpen] = React.useState(false)
  const [mobileOpen, setMobileOpen] = React.useState(false)
  const [activeMenu, setActiveMenu] = React.useState(null)

  const MENU_ITEMS = [
    { label: 'Home', to: '/' },
    { label: 'Tools', to: '/tools' },
    {
      label: 'Courses', to: '/courses', children: [
        { label: 'All Courses', to: '/courses/' },
        { label: 'STR Foundations Master Program', to: '/courses/str-foundations/' }
      ]
    },
    { label: 'Blogs', children: BLOG_CATEGORIES.map(name => ({ label: name, to: `/blog/category/${slugifyCategory(name)}/` })) },
    { label: 'Contact', to: '/contact' },
  ]
  return (
    <header className="sticky top-0 z-50 bg-white/90 border-b border-[var(--color-editorial-border)] backdrop-blur">
      <div className="max-w-[1200px] mx-auto px-4 pt-8 pb-6">
        <div className="flex items-center justify-between gap-8">
          {/* Mobile toggle - moved to the left on small screens */}
          <div className="mobile-toggle md:hidden mr-3">
            <button
              aria-label={mobileOpen ? "Close menu" : "Open menu"}
              onClick={() => setMobileOpen(open => !open)}
              className="p-3 rounded-md border border-[var(--color-editorial-border)] bg-white"
            >
              {mobileOpen ? <X className="w-5 h-5" /> : <Menu className="w-5 h-5" />}
            </button>
          </div>

          <Link to="/" className="no-underline flex-shrink-0 flex items-center gap-2">
            {/* Logo Image */}
            <img
              src="/images/logos/favicon-32x32.png"
              alt="HostHustle Logo"
              className="w-12 h-12"
              style={{ flexShrink: 0 }}
            />

            {/* Text Container */}
            <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-end', lineHeight: 1 }}>
              <span
                style={{
                  fontSize: "clamp(1.5rem, 4vw, 2rem)",
                  letterSpacing: "0.04em",
                  fontWeight: 600,
                  marginBottom: "0.15rem",
                  color: "var(--color-editorial-black)",
                  display: "block",
                  whiteSpace: "nowrap"
                }}
              >
                HostHustle
              </span>
              <span
                style={{
                  fontSize: "clamp(0.65rem, 1.5vw, 0.75rem)",
                  color: "#10b981",
                  fontWeight: 500,
                  letterSpacing: "0.28em",
                  display: "block",
                  whiteSpace: "nowrap"
                }}
              >
                Hosting Dehustled!
              </span>
            </div>
          </Link>

          <nav
            className="desktop-nav hidden md:flex items-center justify-center flex-1"
            style={{ fontSize: "1rem", letterSpacing: "0.24em" }}
          >
            <div className="flex items-center gap-8 text-[var(--color-editorial-black)] font-semibold">
              <Link to="/" className="hover:text-[var(--color-editorial-accent)] transition-colors">HOME</Link>
              <Link to="/tools" className="hover:text-[var(--color-editorial-accent)] transition-colors">TOOLS</Link>

              {/* Courses Dropdown */}
              <div
                className="relative"
                onMouseEnter={() => setIsCoursesOpen(true)}
                onMouseLeave={() => setIsCoursesOpen(false)}
              >
                <Link
                  to="/courses"
                  className="hover:text-[var(--color-editorial-accent)] transition-colors"
                >
                  COURSES
                </Link>

                {isCoursesOpen && (
                  <div
                    className="absolute left-1/2 mt-3 bg-white border border-[var(--color-editorial-border)] rounded editorial-shadow"
                    style={{ transform: "translateX(-50%)", zIndex: 60, minWidth: "280px" }}
                  >
                    <div className="px-4 py-3">
                      <div className="flex flex-col gap-2">
                        <Link
                          to="/courses/"
                          className="text-[var(--color-editorial-black)] hover:text-[var(--color-editorial-accent)] transition-colors font-semibold py-2"
                          style={{ fontSize: "0.75rem", letterSpacing: "0.08em" }}
                        >
                          ALL COURSES
                        </Link>
                        <div className="border-t border-[var(--color-editorial-border)] my-1"></div>
                        <Link
                          to="/courses/str-foundations/"
                          className="text-[var(--color-editorial-black)] hover:text-[var(--color-editorial-accent)] transition-colors font-semibold py-2"
                          style={{ fontSize: "0.75rem", letterSpacing: "0.08em" }}
                        >
                          STR FOUNDATIONS MASTER PROGRAM
                        </Link>
                      </div>
                    </div>
                  </div>
                )}
              </div>

              {/* Blogs Dropdown */}
              <div
                className="relative"
                onMouseEnter={() => setIsBlogOpen(true)}
                onMouseLeave={() => setIsBlogOpen(false)}
              >
                <Link
                  to="/blog"
                  className="hover:text-[var(--color-editorial-accent)] transition-colors"
                >
                  BLOGS
                </Link>

                {isBlogOpen && (
                  <div
                    className="absolute left-1/2 mt-3 bg-white border border-[var(--color-editorial-border)] rounded editorial-shadow"
                    style={{ transform: "translateX(-50%)", zIndex: 60 }}
                  >
                    <div className="px-4 py-3">
                      <div className="flex items-center gap-6">
                        {BLOG_CATEGORIES.map(name => {
                          const slug = `/blog/category/${slugifyCategory(name)}/`
                          return (
                            <Link
                              key={slug}
                              to={slug}
                              className="text-[var(--color-editorial-black)] hover:text-[var(--color-editorial-accent)] transition-colors whitespace-nowrap font-semibold"
                              style={{ fontSize: "0.75rem", letterSpacing: "0.08em" }}
                            >
                              {name.toUpperCase()}
                            </Link>
                          )
                        })}
                      </div>
                    </div>
                  </div>
                )}
              </div>

              <Link to="/contact" className="hover:text-[var(--color-editorial-accent)] transition-colors">CONTACT</Link>
            </div>
          </nav>

          <div className="flex items-center gap-6 flex-shrink-0">
            <div className="hidden md:flex items-center gap-4 text-[var(--color-editorial-black)]">
              <button type="button" className="cursor-pointer w-11 h-11 flex items-center justify-center" aria-label="Facebook">
                <i className="fab fa-facebook-f" />
              </button>
              <button type="button" className="cursor-pointer w-11 h-11 flex items-center justify-center" aria-label="Twitter">
                <i className="fab fa-twitter" />
              </button>
              <button type="button" className="cursor-pointer w-11 h-11 flex items-center justify-center" aria-label="Instagram">
                <i className="fab fa-instagram" />
              </button>
            </div>
          </div>
        </div>
      </div>
      {/* Mobile right-side panel + overlay */}
      {mobileOpen && (
        <>
          <div
            className="md:hidden fixed inset-0 bg-black/30 z-40"
            onClick={() => setMobileOpen(false)}
            aria-hidden
          />

          <aside className="md:hidden fixed top-0 left-0 h-full w-full max-w-[320px] sm:max-w-[420px] bg-white z-50 shadow-lg overflow-auto mobile-aside" style={{ backgroundColor: 'rgba(255,255,255,0.10)', backdropFilter: 'saturate(120%) blur(6px)' }}>
            <div className="px-4 py-4 flex items-center justify-between border-b border-[var(--color-editorial-border)]">
              <div className="text-lg font-semibold">Menu</div>
              <button aria-label="Close menu" onClick={() => { setMobileOpen(false); setActiveMenu(null) }} className="p-2 rounded-md">
                <X className="w-5 h-5" />
              </button>
            </div>

            <div className="px-2 py-3 mobile-menu-columns" style={{ display: 'flex', gap: '0', minHeight: '100%' }}>
              {/* Left column: top-level menus */}
              <nav aria-label="Mobile main navigation" style={{ width: activeMenu ? '55%' : '100%', borderRight: activeMenu ? '1px solid var(--color-editorial-border)' : 'none' }}>
                <ul className="flex flex-col p-3" style={{ gap: '0.25rem' }}>
                  {MENU_ITEMS.map(item => (
                    <li key={item.label}>
                      {item.children ? (
                        <button
                          type="button"
                          onClick={() => setActiveMenu(activeMenu === item.label ? null : item.label)}
                          className="w-full text-left font-semibold text-[var(--color-editorial-black)] py-3 px-2"
                        >
                          {item.label}
                        </button>
                      ) : (
                        <Link to={item.to} className="w-full block text-[var(--color-editorial-black)] py-3 px-2" onClick={() => { setMobileOpen(false); setActiveMenu(null) }}>{item.label}</Link>
                      )}
                    </li>
                  ))}
                </ul>
              </nav>

              {/* Right column: submenu for active top-level item (render only when activeMenu is set) */}
              {activeMenu && (
                <div style={{ width: '45%' }}>
                  <div className="p-3">
                    <div className="text-sm text-[var(--color-editorial-grey)] mb-2">{activeMenu}</div>
                    <ul className="flex flex-col">
                      {(MENU_ITEMS.find(m => m.label === activeMenu)?.children || []).map(sub => (
                        <li key={sub.label}>
                          <Link to={sub.to} className="block py-2 px-2 text-[var(--color-editorial-black)]" onClick={() => { setMobileOpen(false); setActiveMenu(null) }}>{sub.label}</Link>
                        </li>
                      ))}
                    </ul>
                  </div>
                </div>
              )}
            </div>
          </aside>
        </>
      )}
    </header>
  )
}

export default Header
